@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Lobster&family=Sora&display=swap);
.cover_page {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.cover_page img {
    width: 400px;
    height: 400px;
}

.cover_text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cover_text h1 {
    text-align: center;        
    margin-bottom: 20px;
}

.cover_text div {
    font-size: 50px;
    font-style: italic;
    color: rgb(71, 71, 255);
    margin-bottom: 35px;
    font-family: 'Kaushan Script', cursive;
}

.cover_text p {
    width: 80%;
    text-align: center;
}

.cover_text button {
    margin-top: 30px;
    border-radius: 10px;
    background-color: blue;
    color: white;
    padding: 5px 10px ;
}
* {
  font-family: "Sora", sans-serif;
}

.add-btn {
  margin-top: 30px;
  border-radius: 10px;
  border-color: blue;
  padding: 5px 10px;
}

.all_nft {
  display: flex;
  flex-wrap: wrap;
}

.cert_container {
  border: solid 3px green;
  width: 600px;
  height: 450px;
  position: relative;
}

.cert_reps_img {
  width: 80px;
  height: 80px;
  position: absolute;
}

.cert_image {
  width: 100%;
  height: 100%;
}

.cert_details {
  position: absolute;
  background-color: white;
  width: 100%;
  height: 70%;
  bottom: 0;
  border-top-left-radius: 40%;
  border-top-right-radius: 40%;
  opacity: 0;
  transition: 1s all ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: "Lobster", cursive;
}

.cert_details:hover {
  opacity: 1;
}

.cert_name {
  font-size: 25px;
  font-family: "Lobster", cursive;
  margin-bottom: 20px;
  text-align: center;
  padding: 20px 60px;
}

.cert_desc {
  font-family: "Lobster", cursive;
}

.cert_reps {
  font-size: 18px;
  padding: 10px 20px;
  background-color: rgb(129, 129, 129);
  color: white;  
  border-radius: 50%;
}

.cert_act {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-top: 30px;
  align-items: center;
}

.cert_act button {
  border-radius: 5px;
  background-color: rgb(2, 2, 133);
  color: white;
  padding: 7px 15px;
}

.nav {
    height: 170px;    
}

.logo_img {
    height: 110px;
    width: 120px;
}

.nav-item {
    display: flex;
    justify-content: center;
    align-items: center;    
}

.logo_name {
    font-size: 50px;
    font-family: 'Kaushan Script', cursive;
    color: rgb(71, 71, 255);    
}

.nav-item button {
    border-radius: 5px;
    padding: 5px 10px;
    border-color: blue;
    background-color: white;
}
